body{
  margin: 0;
  padding: 0;
  background-color: black;
  color: white;
}

#Header-wrapper{
  background-color: black;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 10px;
  z-index: 2px;
  border-bottom: 2px solid  white;
}
#logo-container{
  cursor: pointer;
  width: 100px;
}

#logo-container img{
  width: 100%;
}
#drop-down-container{
  width:40px;
  display: block;

}
#drop-down-container img{
  width:100%;

}

#image-wrapper{
  position: relative;
  top: -30px;
  z-index: -1;
  
  
  
}
#hot{
  text-align: center;
  font-size: 40px;
}

#image-wrapper img{
  width: 100vw;
  object-fit: contain;
  
}
#list{
  list-style-type: none;
  font-size: 17px;
  line-height: 30px;
  z-index: 90px;
}
#menu-sm{
  background-color: black;
  position: absolute;
  top: 50px;
 right: 0px;
 padding: 10px;
  color: white;
}
.show{
  display: block;
}
.hide{
  display: none;
}
#single-wrapper{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
#singleMovie-container{
  width: 150px;
  text-align: center;
  margin: 7px 10px;
  
}
#singleMovie-container img {
  width: 100%;
  padding: 0;
  margin: 0;
}
#singleMovie-container h6{
  padding: 0;
  margin: 0;
  color: white;
  cursor: pointer;
}
#most-watched{
  color: white;
  text-align: center;
}

 #videodis-container{
  width: 70%;
  margin: 0 auto;
 }
 #videodis-container video{
  width: 100%;
  color: white;
  z-index: -11px;
 }

 h2,p,h4{
  color: white;
  text-align: justify;
  cursor: pointer;
 }
 li{
  cursor: pointer;
 }
 li:hover{
  color: rgba(255, 0, 0, 0.699);
  font-weight: bolder;
  transition: 0.1s;
 }
 #navbar li{
  width: 100%;
 display: inline;
 }
 #slider{
  display: flex;
  align-content: center;
 }

#nav-wrapper{
  display: none;
}
#Footer-wrapper{
  text-align: center;
}
#Footer-wrapper h1{
  font-size: 20px;
}
 @media (min-width:600px) {
  
   #list{
  
    font-size: 20px;
    line-height: 30px;
  }
 }

 @media (min-width:768px) {

   #singleMovie-container{
    width: 160px;

  }
 }

 @media (min-width:992px) {
  #nav-wrapper{
    display: block;
    margin-top: 20px;
  }
#image-wrapper{
  position: relative;
  top: -30px;
  z-index: -1;
  text-align: center;
  
  
  
}

#nav-wrapper{
font-size: 20px;
line-height: 49px;
padding-left: 30px;
  width: 30%;
 }

#image-wrapper img{
  width: 60vw;
  height: 70vh;
}

#singleMovie-container{
  width: 250px;
  text-align: center;
  margin: 7px 20px;
  
}

#slider{
  display: flex;
  align-content: center;

  height: 500px;
 }
 #drop-down-container{
  display: none;
}

 }


 